.wrapperWrapper {
  width: 100%;
  height: 48px;
}

.wrapper {
  position: relative;
  height: 48px;
  display: flex;
  flex: 1;
  width: 100%;
  align-items: flex-end;
}

.span {
  position: absolute;
  top: -1px;
  color: transparent;
  transform: scale(0);
  transition: 0.2s ease-in;
}

.show {
  color: #000;
  transform: scale(100%);
}

.label {
  display: flex;
  align-items: center;
  flex: 1;
  width: 100%;
}

.saveButton {
  position: absolute;
  width: 52px;
  height: 24px;
  right: 12px;
  bottom: 5px;
}

.error {
  position: absolute;
  bottom: -22px;
}
