* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  color: #000;
  font-size: 20px;
}

a {
  text-decoration: none;
  color: black;
}

ul,
ol {
  list-style: none;
}

.app {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #938f8f;
  min-height: 100vh;
  width: 100vw;
}
